*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.center{
  max-width: 960px;
  margin: 0 auto;
  padding: 0 2%;
  display: flex;
  flex-wrap: wrap;
}

.main{
  padding: 40px 0;
  width: 100%;
  background-color: blueviolet;
  background-image: url("./img/fundo.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 400px;
}

.menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.menu h3{
  color: #fff;
}

.menu a{
  color: #fff;
  text-decoration: none;
  border: 1px solid #fff;
  padding: 3px 8px;
  border-radius: 5px;
}

.form{
  width: 100%;
  margin: 90px auto;
  display: flex;
  max-width: 600px;
  justify-content: center;
  flex-wrap: wrap;
}

.form h2{
  color: #fff;
  text-transform: uppercase;
}

.items-form{
  margin: 0 auto;
  max-width: 540px;
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
}

.items-form input[type="text"]{
  width: 48%;
  height: 30px;
  margin: 8px 0px;
  padding-left: 10px;
}

.items-form input[type="button"]{
  width: 99%;
  height: 40px;
  background-color: #e7426b;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
}


@media screen and (max-width: 768px){
  .items-form input[type="text"],
  .items-form input[type="button"]{
    width: 100%;
  }
}

.conteudo-single{
  width: 400px;
  margin: 40px auto;
}

.img-servicos{
  padding: 20px 0;
  width: 400px;
  background-color: blueviolet;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 150px;
}
.img-rede{
  background-image: url("./img/infra.png");
}
.img-formata{
  background-image: url("./img/formata.png");
}
.img-seguranca{
  background-image: url("./img/seguranca.png");
}
.img-design{
  background-image: url("./img/design.png");
}
.container-sobre{
   background: linear-gradient(68.15deg, #2334cc 16.62%, #b82981 85.61%); 
}
.sobre-nos{
  color: #fff;
}
.justifi{
  text-align: justify;
}
.sobre-nos h2{
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 2%;
  display: flex;
  flex-wrap: wrap;
}